import { requestUrl } from "../services/request/request";
import SomethingWentWrong from "../components/opps/Opps";
import Loader from "../components/loader/loader";
import Page from "../components/page/Page";
import { useEffect, useState } from "react";
import { ajax } from "jquery";

export default function CoursesPage() {

    const [state,setState] = useState({
        show: <Loader />
    })

    function showCourse(url) {
        window.open(`${window.location.origin}/coursePage?course=${url}`, '_blank');
    }

    function showCourses() {
        ajax({
            method: "POST",
            url: `${requestUrl}request/courses.php`,
            success: function (response) {
                const courses = JSON.parse(response);
                let coursesTree = <main id="main" data-aos="fade-in" className="aos-init aos-animate">
                <br />
                <br />
                    <section id="courses" className="courses">
                        <div className="container aos-init aos-animate" data-aos="fade-up">
                            <div className="row aos-init aos-animate" data-aos="zoom-in" data-aos-delay="100">
                                {courses.map(course => {
                                    return <div key={course.name} className="col-lg-4 col-md-6 d-flex align-items-stretch" onClick={() => { showCourse(course.url) }}>
                                        <div className="course-item colss">
                                            <img src={`${requestUrl}images/icons/${course.icon}`} className="img-fluid courseImage toolImage" alt="Web Editor" />
                                            <div className="course-content">
                                                <h3>{course.name}</h3>
                                                <p>{course.description}</p>
                                                <div className="d-flex justify-content-between align-items-center mb-3">
                                                    <h4 className="app-btn">Start</h4>
                                                    <p className="price"><small>{course.domain}</small></p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                })}
                            </div>
                        </div>
                    </section >
                </main>;
                setState({...state, show: coursesTree})
            },
            error: function () {
                setState({...state, show: <SomethingWentWrong />})
            }
        });
    }

    useEffect(() => {
        showCourses();
    }, []);

    return <Page>
        {state.show}
    </Page >
}