function toHex(s) {
    var s = unescape(encodeURIComponent(s))
    var h = ''
    for (var i = 0; i < s.length; i++) {
        h += s.charCodeAt(i).toString(16)
    }
    return h
}

function fromHex(h) {
    var s = ''
    for (var i = 0; i < h.length; i+=2) {
        s += String.fromCharCode(parseInt(h.substr(i, 2), 16))
    }
    return decodeURIComponent(escape(s))
}

function write(key, value) {
    localStorage.setItem(toHex(key), toHex(JSON.stringify(value)));
}

function read(key) {
    return JSON.parse(fromHex(localStorage.getItem(toHex(key))));
}

function remove(key) {
    localStorage.removeItem(toHex(key));
}

function exists(key) {
    return localStorage.getItem(toHex(key)) != null;
}

const userStorageKey = 'SPUS11235';

export {read, write, remove, exists, userStorageKey}