import { exists, remove, userStorageKey, write, read } from "./loginUtil";

function logUserIn({name, familyName, givenName, email, sub, picture}) {
    write(userStorageKey, {
        name: name,
        familyName: familyName,
        givenName: givenName,
        email: email,
        UID: sub,
        picture: picture,
    });
}

function logUserOut() {
    remove(userStorageKey);
}

function getUserDetails() {
    return read(userStorageKey);
}

function userSignedIn() {
    return exists(userStorageKey);
}

export {logUserIn, logUserOut, getUserDetails, userSignedIn}