import Navigation from "../../components/navigation/Navigation";
import { useEffect, useState } from "react";
import Editor from "@monaco-editor/react";

const CodeEditorWindow = ({ onChange, language, className, id, defaultValue }) => {
    const [value, setValue] = useState(defaultValue || "");

    const handleEditorChange = (value) => {
        setValue(value);
        onChange(value);
    };

    return <div className="editorwindoweb" id={id || "MUX"}>
        <Editor
            height="90vh"
            width={`100%`}
            language={language || "javascript"}
            value={value}
            defaultValue={value}
            onChange={handleEditorChange}
            className={className}
        />
    </div>
};

export default function ToolWebEditor() {

    let init = {
        html: `<html>\n   <head>\n      <title>Web Page</title>\n   </head>\n   <body>\n      <h2>Hello html!</h2>\n   </body>\n<html>`,
        css: `body {\n   font-family: sans-serif;\n}`,
        javascript: `var number = 7;`,
        mode: `html`,
        o: false,
    }

    if (localStorage.getItem('toolWebEditorSave') == null) {
        localStorage.setItem('toolWebEditorSave', JSON.stringify(init));
    } else {
        init = JSON.parse(localStorage.getItem('toolWebEditorSave'));
    }
    const [state, setState] = useState(init);


    useEffect(function () {
        setTimeout(function () {
            compile(init)
        }, 1000);
    }, []);

    function onChange(v, mode) {
        state[state.mode] = v;
        compile(state);
    }

    function compile({ html, css, javascript }) {
        const code = `
            <style>${css}</style>
            <script>${javascript}</script>
            ${html}
        `;
        document.getElementById("outputWindow").src = "data:text/html;charset=utf-8," + code;
        if (JSON.stringify(state).length <= 2048) {
            state.o = false;
            localStorage.setItem('toolWebEditorSave', JSON.stringify(state));
        } else {
            state.o = true;
        }
        setState({...state});
    }

    function changeMode(m) {
        setState({ ...state, mode: m });
    }
    const htmlEditor = <CodeEditorWindow onChange={(v) => { onChange(v, "html") }} language={"html"} defaultValue={state.html} id="html" />;
    const cssEditor = <CodeEditorWindow onChange={(v) => { onChange(v, "css") }} language={"css"} defaultValue={state.css} id="css" />;
    const jsEdiotr = <CodeEditorWindow onChange={(v) => { onChange(v, "javascript") }} language={"javascript"} defaultValue={state.javascript} id="javascript" />;

    return <div>
        <Navigation />
        <div className="webEditorView">
            <div className="editor-wrap-web">
                <div className="tasWebEditor">
                    <div className="tabWebEditor" id="defaultView" onClick={() => { changeMode('html') }}>HTML</div>
                    <div className="tabWebEditor" onClick={() => { changeMode('css') }}>CSS</div>
                    <div className="tabWebEditor" onClick={() => { changeMode('javascript') }}>JS</div>
                </div>
                {(state.mode == 'css') ? cssEditor : <span></span>}
                {(state.mode == 'javascript') ? jsEdiotr : <span></span>}
                {(state.mode == 'html') ? htmlEditor : <span></span>}
            </div>
            {(state.o)? <span className="webErrorLimit">Source code size limit exceeded! changes will not be saved</span> : <span></span>}
            <iframe className="outputWindow" id="outputWindow"></iframe>
        </div>
    </div>
}