import ToolTechnicalCharts from "../../pages/tools/ToolTechnicalCharts";
import ToolKeycodeFinder from "../../pages/tools/KeycodeFinder";
import ToolWebEditor from "../../pages/tools/ToolWebEditor";
import TermsOfServicePage from "../../pages/TermsOfService";
import PrivacyPolicyPage from "../../pages/PrivacyPolicy";
import PageNotFound from "../../pages/PageNotFound";
import CoursesPage from "../../pages/Courses";
import CompetePage from "../../pages/Compete";
import { Navigate } from "react-router-dom";
import ToolsPage from "../../pages/Tools";
import HomePage from "../../pages/Home";
import CoursePage from "../../pages/CoursePage";
import LoginPage from "../../pages/LoginPage";
import DashBoard from "../../pages/Dashboard";

const routes = [
    {
        path: '/',
        page: <Navigate to='/home' />
    },
    {
        path: '*',
        page: <PageNotFound />
    },
    {
        path: '/home',
        page: <HomePage />
    },
    {
        path: '/login',
        page: <LoginPage />
    },
    {
        path: '/tools',
        page: <ToolsPage />
    },
    {
        path: '/dashboard',
        page: <DashBoard />
    },
    {
        path: '/coursePage',
        page: <CoursePage />
    },
    {
        path: '/courses',
        page: <CoursesPage />
    },
    {
        path: '/compete',
        page: <CompetePage />
    },
    {
        path: '/privacyPolicy',
        page: <PrivacyPolicyPage />
    },
    {
        path: '/termsOfService',
        page: <TermsOfServicePage />
    },
    {
        path: '/toolWebEditor',
        page: <ToolWebEditor />
    },
    {
        path: '/toolTechnicalCharts',
        page: <ToolTechnicalCharts />
    },
    {
        path: '/toolKeycodeFinder',
        page: <ToolKeycodeFinder />
    },
]

export default routes;