import Navigation from "../components/navigation/Navigation";
import SomethingWentWrong from "../components/opps/Opps";
import { requestUrl } from "../services/request/request";
import { logUserIn } from "../services/login/login";
import Loader from "../components/loader/loader";
import LoginLogo from "../assets/svg/login.svg";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import jwtDecode from "jwt-decode";
import { ajax } from "jquery";

export default function LoginPage() {

    const [state, setState] = useState({
        show: <div className="error404-wrap">
            <h2>LOGIN</h2>
            <img src={LoginLogo} className="error404 sdfImg" draggable={false} />
            <br />
            Continue With<br /><br />
            <div id="signInDiv"></div>
        </div>
    })

    const navigate = useNavigate();
    var sendData, userObject;

    function handlecallback(response) {
        userObject = jwtDecode(response.credential);

        setState({ ...state, show: <Loader /> });

        ajax({
            method: "POST",
            url: `${requestUrl}request/login.php`,
            data: {
                'uid': userObject.sub,
                'name': userObject.name,
                'email': userObject.email,
                'familyName': userObject.family_name,
                'givenName': userObject.given_name
            },
            success: function (response) {
                // console.log(response);
                if (response == 'SUCCESS') {
                    logUserIn({
                        familyName: userObject.family_name,
                        givenName: userObject.given_name,
                        picture: userObject.picture,
                        email: userObject.email,
                        name: userObject.name,
                        sub: userObject.sub,
                    });
                    navigate('/home');
                    return;
                }
                setState({ ...state, show: <SomethingWentWrong /> })
            },
            error: function () {
                setState({ ...state, show: <SomethingWentWrong /> })
            }
        });
    }

    useEffect(function () {
        /* global google */
        google.accounts.id.initialize({
            client_id: `169988167943-6ftqtd3tljtlbrc6ui4riqdtf51383b8.apps.googleusercontent.com`,
            callback: handlecallback
        });

        google.accounts.id.renderButton(
            document.getElementById("signInDiv"),
            {
                theme: "outline",
                size: "large"
            }
        )
    }, []);

    return <div>
        <Navigation />
        <div className="loginWarp">
            {state.show}
        </div>
    </div>
}