import Navigation from "../components/navigation/Navigation";
import CompeteLogo from "../assets/svg/webWars.svg";

export default function CompetePage() {
    return <div>
        <Navigation />
        <div className="pagecenter">
            <div className="error404-wrap">
                <img src={CompeteLogo} className="error404" draggable={false} />
                <br />
                <br />
                <h2>Web wars 2023!</h2> 
                Registeration Starts 10th Math, 2023<br /><br />
                <span className="app-btn">Coming Soon</span>
            </div>
        </div>
    </div>
}