import Navigation from "../../components/navigation/Navigation";
import { useEffect } from "react";

export default function ToolTechnicalCharts() {

    useEffect(() => {

        eval(`
        new TradingView.widget(
            {
                "autosize": true,
                "symbol": "SKILLING:NASDAQ",
                "interval": "D",
                "timezone": "Asia/Kolkata",
                "theme": "light",
                "style": "1",
                "locale": "in",
                "toolbar_bg": "#f1f3f6",
                "enable_publishing": false,
                "withdateranges": true,
                "allow_symbol_change": true,
                "container_id": "tradingview_5750d"
            });
        `);
    }, []);

    return <div>
        <Navigation />
        <div class="tradingview-widget-container">
            <div id="tradingview_5750d"></div>
            <div className="tradingview-widget-copyright"><a href="https://in.tradingview.com/symbols/SKILLING-NASDAQ/" rel="noopener" target="_blank"><span className="blue-text">Technical Chart</span></a> by TradingView</div>
        </div>
    </div>
}