import { useNavigate } from "react-router-dom";
import logo from './logo.png';
import { getUserDetails, userSignedIn } from "../../services/login/login";

export default function Navigation() {


  const navigate = useNavigate();

  function toogleNavigation() {
    document.getElementById('navbar').classList.toggle('navbar-mobile');
    document.getElementById('toggle').classList.toggle('bi-list')
    document.getElementById('toggle').classList.toggle('bi-x')
  }

  function goToHome() {
    navigate('/home');
  }

  function navigatTo(url) {
    navigate(url);
  }

  function profileButtonClick() {
    navigate('/dashboard');
    console.log(getUserDetails())
  }

  return <header id="header" className="fixed-top">
    <div className="container d-flex align-items-center">

      <a className="logo me-auto" onClick={goToHome}>
        <img src={logo} alt="" className="img-fluid" />
      </a>

      <nav id="navbar" className="navbar order-last order-lg-0">
        <ul>
          {/* <li className="dropdown">
            <a href="#"><span>Courses</span> <i className="bi bi-chevron-down"></i></a>
            <ul>
              <li><a href="#">Drop Down 1</a></li>
              <li><a href="#">Drop Down 2</a></li>
              <li><a href="#">Drop Down 3</a></li>
              <li><a href="#">Drop Down 4</a></li>
            </ul>
          </li> */}
          <li onClick={() => { navigatTo('/home') }}><a>Home</a></li>
          <li onClick={() => { navigatTo('/courses') }}><a>Courses</a></li>
          {/* <li><a>Practise</a></li> */}
          <li onClick={() => { navigatTo('/tools') }}><a>Tools</a></li>
          <li onClick={() => { navigatTo('/compete') }}><a>Compete</a></li>
        </ul>
        <i id="toggle" className="bi bi-list mobile-nav-toggle" onClick={toogleNavigation}></i>
      </nav>

      {(userSignedIn()) ? <div className="userProfileButton" onClick={profileButtonClick}>
        Hi, {getUserDetails().givenName}
        <img className="userImage" src={getUserDetails().picture} />
      </div> :
        <a className="app-btn" onClick={() => { navigatTo('/login') }}>Login</a>}

    </div>
  </header>
}