import Navigation from "../../components/navigation/Navigation";
import { useState } from "react";

export default function ToolKeycodeFinder() {

    const [state, setState] = useState({
        v: ''
    });

    function update(e) {
        setState({...state, v: 'The keycode is '+e.which});
    }

    return <div>
        <Navigation />
        <div className="pagecenter">
            <div className="error404-wrap">
                <span className="headkc">Keycode Finder</span>
                <input onKeyDown={update} className="arlinp" placeholder="Enter a key"/>
                <br />
                <br />
                <smal>{state.v}</smal>
            </div>
        </div>
    </div>
}