import { getUserDetails, logUserOut, userSignedIn } from "../services/login/login";
import { requestUrl } from "../services/request/request";
import Loader from "../components/loader/loader";
import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import Page from "../components/page/Page";
import './pageCss/dashboard.css';
import { ajax } from "jquery";
import SomethingWentWrong from "../components/opps/Opps";

export default function DashBoard() {

    const navigate = useNavigate();
    const [state, setState] = useState({
        courses: 'Loading',
        activeContent: <div className="profileLoader">
            <Loader />
        </div>,
        activeTab: 'profile',
        courses: [],
        events: [],
        certificates: [],
        time: false,
    });
    const style = {
        minHeight: window.innerHeight + 'px',
    }
    const style2 = {
        cursor: "pointer",
    }

    function showTabContent() {
        return {
            profile: function () {
                let details = getUserDetails();
                if (!userSignedIn()) {
                    return <span>Login required</span>
                }
                console.log(state.time)
                return <div className="container">
                    <div className="row profile">
                        <div className="col-md-3">
                            <div className="profile-sidebar">
                                <div className="profile-userpic">
                                    <img src={details.picture} className="img-responsive" alt=" " />
                                </div>
                                <div className="profile-usertitle">
                                    <div className="profile-usertitle-name">
                                        {details.name}
                                    </div>
                                    <div className="profile-usertitle-job">
                                        {details.email}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="profile-content">
                                <div className="field">Profile Name </div> {details.name} <br /><br />
                                <div className="field">Email </div> {details.email} <br /><br />
                                <div className="field">Subscription </div> Complete Access <br /><br />
                                <div className="field">Social logins </div> Google <br /><br />
                                {(state.time == false) ? <span></span> : <span><div className="field">Joined on</div> {state.time.toString()} <br /><br /> </span>}
                            </div>
                        </div>
                    </div>
                </div>;
            },
            courses: function () {
                let details = getUserDetails();
                if (!userSignedIn()) {
                    return <span>Login required</span>
                }
                return <div className="container">
                    <div className="row profile">
                        <div className="col-md-3">
                            <div className="profile-sidebar">
                                <div className="profile-userpic">
                                    <img src={details.picture} className="img-responsive" alt=" " />
                                </div>
                                <div className="profile-usertitle">
                                    <div className="profile-usertitle-name">
                                        {details.name}
                                    </div>
                                    <div className="profile-usertitle-job">
                                        {details.email}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="profile-content">
                                <div className="field">Your Courses </div>
                                {(state.courses.length == 0) ? <span>No courses enrolled</span> :
                                    state.courses.map((course) => {
                                        return <span>{course} <br /></span>
                                    }
                                    )}
                                <br /><br />
                            </div>
                        </div>
                    </div>
                </div>;
            },
            events: function () {
                let details = getUserDetails();
                if (!userSignedIn()) {
                    return <span>Login required</span>
                }
                return <div className="container">
                    <div className="row profile">
                        <div className="col-md-3">
                            <div className="profile-sidebar">
                                <div className="profile-userpic">
                                    <img src={details.picture} className="img-responsive" alt=" " />
                                </div>
                                <div className="profile-usertitle">
                                    <div className="profile-usertitle-name">
                                        {details.name}
                                    </div>
                                    <div className="profile-usertitle-job">
                                        {details.email}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="profile-content">
                                <div className="field">Your Events </div>
                                {(state.events.length == 0) ? <span>No events found</span> :
                                    state.events.map((event) => {
                                        return <span>{event}</span>
                                    }
                                    )}
                                <br /><br />
                            </div>
                        </div>
                    </div>
                </div>;
            },
            certificates: function () {
                let details = getUserDetails();
                if (!userSignedIn()) {
                    return <span>Login required</span>
                }
                return <div className="container">
                    <div className="row profile">
                        <div className="col-md-3">
                            <div className="profile-sidebar">
                                <div className="profile-userpic">
                                    <img src={details.picture} className="img-responsive" alt=" " />
                                </div>
                                <div className="profile-usertitle">
                                    <div className="profile-usertitle-name">
                                        {details.name}
                                    </div>
                                    <div className="profile-usertitle-job">
                                        {details.email}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-9">
                            <div className="profile-content">
                                <div className="field">Your Courses </div>
                                {(state.certificates.length == 0) ? <span>No certificates credited</span> :
                                    state.certificates.map((certificate) => {
                                        return <span>{certificate} <br /></span>
                                    }
                                    )}
                                <br /><br />
                            </div>
                        </div>
                    </div>
                </div>;
            }
        }
    }

    function showTab(tab) {
        setState({ ...state, activeTab: tab, activeContent: showTabContent()[tab]() });
    }

    function logOut() {
        logUserOut();
        navigate('/home');
    }

    useEffect(() => {
        if (userSignedIn()) {
            showTab('profile');
            ajax({
                method: "POST",
                url: `${requestUrl}request/dashboard.php`,
                data: {
                    'uid': getUserDetails().UID,
                    'email': getUserDetails().email,
                },
                success: function (response) {
                    if (response.includes(getUserDetails().email)) {
                        const data = JSON.parse(response);
                        console.log(data.creationDate)
                        setState({
                            ...state,
                            certificates: JSON.parse(data.certificatesField),
                            events: JSON.parse(data.eventsField),
                            courses: JSON.parse(data.coursesField),
                            time: new Date(data.creationDate),
                            activeTab: 'profile',
                            activeContent: showTabContent()['profile'](),
                        });
                        return;
                    }
                    setState({ ...state, activeContent: <SomethingWentWrong /> })
                },
                error: function () {
                    setState({ ...state, activeContent: <SomethingWentWrong /> })
                }
            });
        } else {
            setTimeout(function () {
                navigate('/home');
            }, 5);
        }
    }, []);

    return <Page>
        <div className="dashBoardContainer" style={style}>
            <ul className="nav nav-tabs">
                <li className="nav-item" onClick={() => { showTab('profile'); }}>
                    <a className={(state.activeTab == 'profile') ? "nav-link active" : "nav-link"} >Profile</a>
                </li>
                <li className="nav-item" onClick={() => { showTab('courses'); }}>
                    <a className={(state.activeTab == 'courses') ? "nav-link active" : "nav-link"} >Courses</a>
                </li>
                <li className="nav-item" onClick={() => { showTab('events'); }}>
                    <a className={(state.activeTab == 'events') ? "nav-link active" : "nav-link"} >Events</a>
                </li>
                <li className="nav-item" onClick={() => { showTab('certificates'); }}>
                    <a className={(state.activeTab == 'certificates') ? "nav-link active" : "nav-link"} >Certificates</a>
                </li>
                <li className="nav-item" onClick={() => { logOut() }} style={style2}>
                    <a className="nav-link disabled" ><span className="app-btn logut">LOGOUT</span></a>
                </li>
            </ul>
            {state.activeContent}
        </div>
    </Page>
}