import webEditor from '../assets/svg/web-editor.svg';
import keycode from '../assets/svg/keycode.svg';
import charts from '../assets/svg/charts.svg';
import Page from "../components/page/Page";

export default function ToolsPage() {

    function navigatTo(url) {
        window.open(window.location.origin+url, '_blank');
    }

    return <Page>
        <br />
        <br />
        <main id="main" data-aos="fade-in" className="aos-init aos-animate">
            <section id="courses" className="courses">
                <div className="container aos-init aos-animate" data-aos="fade-up">
                    <div className="row aos-init aos-animate" data-aos="zoom-in" data-aos-delay="100">

                        <div className="col-lg-4 col-md-6 d-flex align-items-stretch" onClick={() => { navigatTo('/toolWebEditor') }}>
                            <div className="course-item colss">
                                <img src={webEditor} className="img-fluid toolImage" alt="Web Editor" />
                                <div className="course-content">
                                    <h3><a href="course-details.html">Web Editor</a></h3>
                                    <p>Edit html, css and javascript in your browser and see the output in real time. Supports integration of external libraries.</p>
                                    <div className="d-flex justify-content-between align-items-center mb-3">
                                        <h4 className="app-btn">Launch</h4>
                                        <p className="price"><small>Web Development</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-6 d-flex align-items-stretch" onClick={() => { navigatTo('/toolTechnicalCharts') }}>
                            <div className="course-item colss">
                                <img src={charts} className="img-fluid toolImage" alt="Technical Charts" />
                                <div className="course-content">
                                    <h3><a href="course-details.html">Technical Charts</a></h3>
                                    <p>Watch and analyse technical charts of various assets including stocks, currencies, crypto and much more. Uses TradingView API.</p>
                                    <div className="d-flex justify-content-between align-items-center mb-3">
                                        <h4 className="app-btn">Launch</h4>
                                        <p className="price"><small>Technical Analysis</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-6 d-flex align-items-stretch" onClick={() => { navigatTo('/toolKeycodeFinder') }}>
                            <div className="course-item colss">
                                <img src={keycode} className="img-fluid toolImage" alt="Technical Charts" />
                                <div className="course-content">
                                    <h3><a href="course-details.html">Keycode Finder</a></h3>
                                    <p>Having trouble with keycodes? Do worry we got you. Find keycodes for keys instantly. Just press a key and all the key information will be shown.</p>
                                    <div className="d-flex justify-content-between align-items-center mb-3">
                                        <h4 className="app-btn">Launch</h4>
                                        <p className="price"><small>Coding</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </section >
        </main>
    </Page >
}